<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <div class="p-3">
          <p class="h1">Daily VS Team</p>
          <DatePicker color="red" title-position="left"
            is-dark show-weeknumbers="right" @dayclick="onDayClick"
            :disabled-dates='{ weekdays: [1, 7] }'
            v-model="todayModel" mode='date'
            :min-date="today"
            :attributes="attrs"
            @update:from-page="(ev) => updatePage(ev)"
            :first-day-of-week="2"
            locale="es"
            />
        </div>
        <div class="p-3">
          <div class="tarjeta p-3">
            <p>
              {{todayModel.toLocaleDateString("es-Es", { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}}
            </p>
            <p> 
              <b>Responsable Daily</b>
              <br/>
              <span class="responsable">
                {{daySel.person.nombre}}
              </span>
              <br/>
              <span class="responsable-backup">
                Backup: {{daySel.backUpSel.nombre}}
              </span>
            </p>
          </div>          
        </div>
        <div class="p-3" v-if="daySel.news.birthdays.length || daySel.news.holiday['nombre'] != undefined || daySel.news.events.length">
          <div class="tarjeta tarjetaRoja p-3">
            <b>Actividades y recordatorios: </b>
            <div v-if="daySel.news.birthdays.length">
              <b><u>Cumpleaños</u></b>
              <ul>
                <li v-for="birthday in daySel.news.birthdays" v-bind:key="birthday">
                  {{ birthday.nombre }}
                  <p>
                    {{birthday.emailPeronal}} - {{birthday.address}}
                  </p>
                </li>
              </ul>
            </div>
            <div v-if="daySel.news.holiday['nombre'] != undefined">
              <b><u>Feriado</u></b>
              <ul>
                <li>
                  {{ daySel.news.holiday.nombre+ " - Irrenunciable: " +(( daySel.news.holiday.irrenunciable === '1') ? 'Si' : 'No') + ' - ' + daySel.news.holiday.tipo}}
                </li>
              </ul>
            </div>
            <div v-if="daySel.news.events.length">
              <b><u>Eventos:</u></b>
              <ul>
                <li v-for="event in daySel.news.events" v-bind:key="event">
                  {{ event }}
                </li>
              </ul>
            </div>  
          </div>
        </div>
        <div class="p-3 sheep-position">
          <Sheep :sheepName="daySel.person.sheepName"/>
        </div>
      </div>
    </div>
    <div v-if="daySel.news.birthdays.length">
      <div class="pyro">
        <div class="before"></div>
        <div class="after"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { DatePicker } from 'v-calendar';
import Team from './json/Team.json';
import Events from './json/Events.json';
import Holiday from './json/Holidays.json';
import ProcesedAttr from './json/procesedAttr.json';
import moment from 'moment';
import Sheep from './Sheep';

export default {
  name: 'DailyCalendar',
  components: {
    DatePicker,
    Sheep
  },
  data() {
    return {
      today: new Date(),
      todayModel: new Date(),
      attrs: [],
      daySel: {
        person: {},
        backUpSel: {},
        news: {
          birthdays:[],
          holiday:{},
          events:[],
        },
      },
      yearPage: (new Date()).getFullYear(),
    };
  },
  async created() {
    this.attrs = ProcesedAttr;
    this.attrs.push({
      key: 'today', bar: true,
      dates: this.today, fillMode: 'solid',
      contentClass: 'italic',
    });
    this.setTurn(moment(this.today).weekday(), moment(this.today).isoWeek());
    this.showNew(parseInt(moment(this.today).format('D')),
      parseInt(moment(this.today).month() +1),
      parseInt(moment(this.today).format('YYYY')));
  },
  methods: {
    onDayClick(day) {
      this.setSelected(day);
      this.showNew(day.day, day.month, day.year);
    },
    setSelected(day) {
      if(day.weekdayPosition < 6){
        this.daySel.day = day;
        this.setTurn(day.weekdayPosition, day.isoWeeknumber);
      }
    },
    genBirthdayAttribute(){
      for (let idx = 0; idx < Team.length; idx++) {
        if(Team[idx]['birthday'] != undefined) {
          let birthday = Team[idx].birthday.month+'-'+Team[idx].birthday.day+'-'
            +this.yearPage+' 04:00:00';
          this.attrs.push({
            dot: true,
            popover: {
              label: 'Cumpleaños: ' + Team[idx].nombre,
            },
            fillMode: 'solid',
            dates: new Date(birthday),
          });
        }      
      }
    },
    getTurnIdx(weekDay, isoWeek){
      let people = Team.filter( r => r.habilitado == 1);
      return (weekDay + ((isoWeek -1) * 5)) % people.length;
    },
    setTurn(weekDay, isoWeek){
      let people = Team.filter( r => r.habilitado == 1);
      let teamIdx = this.getTurnIdx(weekDay, isoWeek);
      let bckUpIdx = (teamIdx + 2) > (people.length -1) ? ((teamIdx + 2) % people.length) : teamIdx + 2;
      this.daySel.person = { nombre: people[teamIdx].nombre, email: people[teamIdx].email, sheepName: people[teamIdx].sheepName};
      this.daySel.backUpSel = { nombre: people[bckUpIdx].nombre, email: people[bckUpIdx].email};
    },
    showNew(_day,_month,_year){
      let selectedDay = new Date(_year, _month -1, _day);
      let selectedDayString = _year+'-'+(_month < 10 ? '0' : '' ) + _month + '-' + (_day < 10 ? '0' : '') + _day;
      this.daySel.news.birthdays = [];
      this.daySel.news.events = [];
      this.daySel.news.holiday = {};
      for (let idx = 0; idx < Team.length; idx++) {
        if(Team[idx]['birthday'] != undefined ) {
          if(Team[idx].birthday.day == _day && Team[idx].birthday.month == _month)
          {
            this.daySel.news.birthdays.push({
                nombre:Team[idx].nombre,
                emailPeronal:Team[idx].emailPeronal,
                address:Team[idx].address
              });
          }
        }
      }
      for (let idx = 0; idx < Events.length; idx++) {
        if(selectedDay >= new Date(Events[idx].start) && selectedDay <= new Date(Events[idx].end)){
          this.daySel.news.events.push(Events[idx].description);
        }
      }
      for (let idx = 0; idx < Holiday.length; idx++) {
        if(Holiday[idx].fecha == selectedDayString){
          this.daySel.news.holiday = Holiday[idx];
        }
      }
    },
    updatePage(ev) {
      if(this.yearPage < ev.year) {
        this.yearPage = ev.year;
        this.genBirthdayAttribute();
      }
    },
  },
  watch: {
    todayModel(newValue, oldValues) {
      if(newValue === null){
        this.todayModel = oldValues;
      }
    },
  }
}
</script>

